import { EventEmitter } from "./index";

export default class Time extends EventEmitter {
  constructor() {

    // 调用父类构造器函数
    super();
    // 将当前时间作为开始时间
    this.start = Date.now();
    // 记录当前时间
    this.current = this.start;
    // 记录已过时间
    this.elapsed = 0;
    // 记录时间间隔，默认16ms(60fps)
    this.delta = 16;

    // 将tick方法绑定为当前实例的方法
    this.tick = this.tick.bind(this);
    // 执行tick方法，开始循环
    this.tick();
  }

  // 用于更新时间参数并触发tick事件
  tick() {
    // 使用requestAnimationFrame方法实现动画循环
    this.ticker = window.requestAnimationFrame(this.tick);
    // 获取当前时间
    const current = Date.now();
    // 计算时间间隔
    this.delta = current - this.current;
    // 计算已过时间
    this.elapsed = current - this.start;
    // 更新当前时间
    this.current = current;
    // 如果时间间隔超过60ms，则将其限制在60ms以内，防止因页面不可见等原因导致的时间间隔过大
    if (this.delta > 60) {
      this.delta = 60;
    }
    // 触发tick事件，通知其它模块时间已更新
    this.trigger("tick");
  }

  // 用于停止动画循环
  stop() {
    // 调用cancelAnimationFrame方法停止动画循环
    window.cancelAnimationFrame(this.ticker);
  }
}
