export default class {
  // 构造函数，初始化回调函数对象，包括一个基础命名空间
  constructor() {
    this.callbacks = {};
    this.callbacks.base = {};
  }

  // 注册事件监听器
  on(_names, callback) {
    const that = this;
    // 检查参数是否有效
    if (typeof _names === "undefined" || _names === "") {
      return false;
    }
    if (typeof callback === "undefined") {
      return false;
    }

    // 解析事件名称
    const names = this.resolveNames(_names);

    // 遍历每个事件名称
    names.forEach(function (_name) {
      // 解析具体事件的命名空间和值
      const name = that.resolveName(_name);
      // 如果命名空间不存在则创建
      if (!(that.callbacks[name.namespace] instanceof Object)) {
        that.callbacks[name.namespace] = {};
      }
      // 如果事件值不存在，则创建
      if (!(that.callbacks[name.namespace][name.value] instanceof Array)) {
        that.callbacks[name.namespace][name.value] = [];
      }
      // 将回调函数添加到对应的命名空间和事件值下
      that.callbacks[name.namespace][name.value].push(callback);
    });
    return this;
  }
  // 移除事件监听器
  off(_names) {
    const that = this;
    // 检查参数是否有效
    if (typeof _names === "undefined" || _names === "") {
      return false;
    }
    // 解析事件名称
    const names = this.resolveNames(_names);

    // 遍历每个事件名称
    names.forEach(function (_name) {
      const name = that.resolveName(_name);
      // 如果命名空间不是基础命名空间且事件值为空，则移除整个命名空间下的所有事件监听器
      if (name.namespace !== "base" && name.value === "") {
        delete that.callbacks[name.namespace];
      } else {
        // 否则根据命名空间和事件值移除对应的事件监听器
        if (name.namespace === "base") {
          for (const namespase in that.callbacks) {
            if (
              that.callbacks[namespase] instanceof Object &&
              that.callbacks[namespase][name.value] instanceof Array
            ) {
              // 移除基础命名空间下指定事件的监听器
              delete that.callbacks[namespase][name.value];
              // 如果命名空间下已经没有事件监听器，则移除整个命名空间
              if (Object.keys(that.callbacks[namespase]).length === 0) {
                delete that.callbacks[namespase];
              }
            }
          }
        } else if (
          that.callbacks[name.namespace] instanceof Object &&
          that.callbacks[name.namespace][name.value] instanceof Array
        ) {
          // 移除指定命名空间下指定事件的监听器
          delete that.callbacks[name.namespace][name.value];
          // 如果命名空间下已经没有事件监听器则移除整个命名空间
          if (Object.keys(that.callbacks[name.namespace]).length === 0) {
            delete that.callbacks[name.namespace];
          }
        }
      }
    });
    return this;
  }

  // 触发事件
  trigger(_name, _args) {
    // 检查参数是否有效
    if (typeof _name === "undefined" || _name === "") {
      return false;
    }
    const that = this;
    let finalResult = null;
    let result = null;
    const args = !(_args instanceof Array) ? [] : _args;

    let name = this.resolveNames(_name);
    name = this.resolveName(name[0]);
    // 如果命名空间是基础命名空间
    if (name.namespace === "base") {
      // 遍历所有命名空间
      for (const namespace in that.callbacks) {
        // 如果命名空间存在且事件值存在
        if (
          that.callbacks[namespace] instanceof Object &&
          that.callbacks[namespace][name.value] instanceof Array
        ) {
          // 遍历事件值下的每个回调函数并执行
          that.callbacks[namespace][name.value].forEach(function (callback) {
            result = callback.apply(that, args);
            // 如果finalResult是未定义，则将结果赋给它
            if (typeof finalResult === "undefined") {
              finalResult = result;
            }
          });
        }
      }
    } else if (this.callbacks[name.namespace] instanceof Object) {
      // 如果命名空间存在
      if (name.value === "") {
        return this;
      }
      // 遍历事件值下的每个回调函数，并执行
      that.callbacks[name.namespace][name.value].forEach(function (callback) {
        result = callback.apply(that, args);
        // 如果finalResult未定义则将结果赋给它
        if (typeof finalResult === "undefined") {
          finalResult = result;
        }
      });
    }
    // 返回最终结果
    return finalResult;
  }

  // 解析事件名称，将其转换为数组形式
  resolveNames(_names) {
    let names = _names;
    // 移除非法字符
    names = names.replace(/[^a-zA-Z0-9 ,/.]/g, "");
    // 将逗号和斜杠替换为空格
    names = names.replace(/[,/]+/g, " ");
    // 拆分为数组
    names = names.split(" ");
    return names;
  }

  // 解析事件名和命名空间
  resolveName(name) {
    const newName = {};
    const parts = name.split(".");
    // 记录原始命名
    newName.original = name;
    // 取得事件值
    newName.value = parts[0];
    // 设置默认命名空间为基础命名空间
    newName.namespace = "base";
    // 如果有命名空间则赋值为相应的命名空间
    if (parts.length > 1 && parts[1] !== "") {
      newName.namespace = parts[1];
    }
    return newName;
  }
}
